@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');
*{
  font-family: 'Russo One', sans-serif;
  font-size: 1.7rem;
}
body{
  background: linear-gradient(90deg, rgba(0,212,255,1) 0%, rgba(205,222,29,1) 100%);

}

.form-input{
  background:none;
  width: 95vw;
}

.card-grid  {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.card{
  box-shadow: 3px 3px 22px -8px rgba(0,0,0,0.71);
  overflow: hidden;
  border-radius: 10px;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
  margin: 0 1rem 1rem;
  background: linear-gradient(90deg, rgba(205,222,29,.7) 0%, rgba(22,74,72,.7) 50%, rgba(0,212,255,.7) 100%);

}
.card p{
  text-align:center;
  font-size: 1rem;
}
h3{
  margin-left: 2rem;
}
.card img{
  width: 100%;
}
